@-webkit-keyframes fadeIn {
  /* the fadeIn delay is set by adjusting the middle percentage in conjuction with the full sequence duration length above. */
  0% {
    opacity: 0; }
  /* at 0% completion of sequence the opcacity is set to 0 = 0% visible */
  50% {
    opacity: 0; }
  /* at 30% completion of sequence the opcacity is set to 0 = 0% visible  - the opacity starts the fade in at 30% sequence completion*/
  100% {
    opacity: 1; }
  /* at 100% completion of sequence the opcacity is set to 1 = 100% visible - thus completeing the fade sequence*/ }

@keyframes fadeIn {
  /* the fadeIn delay is set by adjusting the middle percentage in conjuction with the full sequence duration length above. */
  0% {
    opacity: 0; }
  /* at 0% completion of sequence the opcacity is set to 0 = 0% visible */
  50% {
    opacity: 0; }
  /* at 30% completion of sequence the opcacity is set to 0 = 0% visible  - the opacity starts the fade in at 30% sequence completion*/
  100% {
    opacity: 1; }
  /* at 100% completion of sequence the opcacity is set to 1 = 100% visible - thus completeing the fade sequence*/ }

